<style lang="less" scoped>
@import "../css/common.less";
.container {
  background: #fff;
  //   顶部头部开始
  .top-header {
    padding: 10px;
    .img {
      width: 100px;
      height: 100px;
      background: pink;
      border-radius: 100px;
    }
  }

  //列表栏开始
  .items {
    padding: 30px 25px;
    background: #fff;
    .item {
      border-bottom: 1px solid #ddd;
      padding: 10px 5px;
      margin-bottom: 10px;
    }
    :nth-child(4) {
      margin-top: 30px;
    }
  }
}
</style>
<template>
    <div class="container flex">
        <div class="top-header flex center">
            <div class="img">
                <img src="">
            </div>
            <h2>昵称</h2>
            <p>个人资料完成度10%</p>
        </div>
        <ul class="items flex">
            <li @click="jump(item.linkTo)" class="item flex space row" v-for="(item,index) in listConfig" :key="index">
                <span>{{item.title}}</span>
                <span>></span>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  data() {
    return {
      listConfig: [
        {
          title: "我的订单",
          linkTo: "xxx"
        },
        {
          title: "我的保单",
          linkTo: "xxx"
        },
        {
          title: "我的理赔",
          linkTo: "xxx"
        },
        {
          title: "账户管理",
          linkTo: "xxx"
        },
        {
          title: "设置",
          linkTo: "xxx"
        }
      ]
    };
  },
  methods: {
    jump(params) {}
  }
};
</script>



